import { MultiStopwatchState, MultiStopwatchUtils, toCSV, toXLSX, useMultiStopwatch } from 'utils';
import {
  Box,
  Button,
  Center,
  IconButton,
  Table,
  TableContainer,
  Tbody,
  Text,
  Textarea,
  Th,
  Thead,
  Tr,
} from '@chakra-ui/react';
import { Link } from 'gatsby';
import { AddIcon, ArrowBackIcon } from '@chakra-ui/icons';
import MultiStopwatchControls from './multiStopwatchControls';
import StopwatchRow from './stopwatchRow';
import React from 'react';

import * as XLSX from 'xlsx-js-style';

export default ({ id }: { id: string }) => {
  const {
    createStopwatch,
    removeStopwatch,
    multiStopwatch,
    stopAll,
    stop,
    startAll,
    resetAll,
    lap,
    setStopwatchName,
    setNotes,
  } = useMultiStopwatch(id);

  const exportCSV = () => {
    const csv = toCSV(multiStopwatch);
    const blob = new Blob([csv], { type: 'text/csv' });
    const link = document.createElement('a');
    console.log('Downloading');
    const url = URL.createObjectURL(blob);
    link.setAttribute('href', url);
    link.setAttribute('download', `${multiStopwatch.name}.csv`);
    link.style.visibility = 'hidden';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const exportXLSX = () => {
    const workbook = toXLSX(multiStopwatch);
    const buffer = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
    const blob = new Blob([buffer], {
      type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
    });

    const link = document.createElement('a');
    console.log('Downloading');
    const url = URL.createObjectURL(blob);
    link.setAttribute('href', url);
    link.setAttribute('download', `${multiStopwatch.name}.xlsx`);
    link.style.visibility = 'hidden';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  return (
    <Box m={1}>
      <Link to={'/'}>
        <ArrowBackIcon boxSize={8} />
      </Link>

      <MultiStopwatchControls
        multiStopwatch={multiStopwatch}
        stopAll={stopAll}
        startAll={startAll}
        resetAll={resetAll}
      />

      <TableContainer>
        <Table border={'1px'} size={'sm'}>
          <Thead>
            <Tr>
              <Th
                width={multiStopwatch.state == MultiStopwatchState.NOT_STARTED ? '15em' : '8em'}
              />
              <Th width={'5em'}>
                {multiStopwatch.state !== MultiStopwatchState.NOT_STARTED ? 'Time' : null}
              </Th>
              {Array.from({ length: MultiStopwatchUtils.maxLaps(multiStopwatch) }).map(
                (_, index, arr) => (
                  <Th key={index} width={'5em'}>
                    <Center>{arr.length - index}</Center>
                  </Th>
                ),
              )}
              <Th />
            </Tr>
          </Thead>
          <Tbody>
            {multiStopwatch.stopwatches.map((sw, index) => (
              <StopwatchRow
                key={index}
                sw={sw}
                index={index}
                stop={() => stop(index)}
                lap={() => lap(index)}
                remove={() => removeStopwatch(index)}
                setName={name => setStopwatchName(index, name)}
                maxLaps={MultiStopwatchUtils.maxLaps(multiStopwatch)}
              />
            ))}
          </Tbody>
        </Table>
      </TableContainer>

      {multiStopwatch.state == MultiStopwatchState.NOT_STARTED && (
        <IconButton
          colorScheme={'green'}
          aria-label={'Add Stopwatch'}
          onClick={createStopwatch}
          icon={<AddIcon />}
          mt={2}
        />
      )}

      <Text mt={2}>Notes</Text>
      <Textarea
        w={'100%'}
        placeholder={'Write some notes here...'}
        value={multiStopwatch.notes}
        onChange={e => setNotes(e.target.value)}
      />

      {multiStopwatch.state == MultiStopwatchState.COMPLETE && (
        <Button onClick={exportCSV} mt={2}>
          Export CSV
        </Button>
      )}
      <Button onClick={exportXLSX} mt={1}>
        Export XLSX
      </Button>
    </Box>
  );
};
