import { formatMs, Stopwatch, StopwatchState, StopwatchUtils } from 'utils';
import { Center, Td, Text, Tr, VStack } from '@chakra-ui/react';
import RowControls from './rowControls';
import LapData from './lapData';
import React, { useEffect, useState } from 'react';

interface StopwatchRowProps {
  sw: Stopwatch;
  index: number;
  stop: () => void;
  lap: () => void;
  remove: () => void;
  setName: (name: string) => void;
  maxLaps: number;
}

export default ({ sw, index, stop, lap, remove, setName, maxLaps }: StopwatchRowProps) => (
  <Tr>
    <Td m={0} p={0}>
      <Center p={0.5} ml={1} mr={1}>
        <RowControls
          sw={sw}
          stop={stop}
          lap={lap}
          index={index}
          remove={remove}
          setName={setName}
        />
      </Center>
    </Td>

    {sw.state !== StopwatchState.NOT_STARTED && (
      <>
        <CurrentTime sw={sw} />
        {Array.from({ length: maxLaps }).map((_, i) =>
          maxLaps - i - 1 < sw.laps.length ? (
            <LapData
              key={maxLaps - i}
              laps={sw.laps}
              startTime={sw.startTime}
              index={maxLaps - i - 1}
            />
          ) : (
            <Td />
          ),
        )}
      </>
    )}
  </Tr>
);

const CurrentTime = ({ sw }: { sw: Stopwatch }) => {
  const [now, setNow] = useState<number>(Date.now());

  useEffect(() => {
    const intervalId = setInterval(() => {
      setNow(Date.now());
    }, 20);

    return () => clearInterval(intervalId);
  }, []);

  return (
    <Td m={0} p={0}>
      <VStack spacing={0}>
        <Text fontSize={'xl'} as={'b'}>
          {formatMs(StopwatchUtils.elapsed(sw, now))}
        </Text>
        <Text fontSize={'sm'}>{formatMs(StopwatchUtils.currentLap(sw, now))}</Text>
      </VStack>
    </Td>
  );
};
