import { Button, ButtonGroup, Center } from '@chakra-ui/react';
import { MultiStopwatch, MultiStopwatchState } from 'utils';
import React from 'react';

export default ({
  multiStopwatch,
  startAll,
  stopAll,
  resetAll,
}: {
  multiStopwatch: MultiStopwatch;
  startAll: () => void;
  stopAll: () => void;
  resetAll: () => void;
}) => (
  <Center mb={2} mt={2}>
    {multiStopwatch.state == MultiStopwatchState.NOT_STARTED ? (
      <Button
        size={'lg'}
        colorScheme={'green'}
        isDisabled={multiStopwatch.state != MultiStopwatchState.NOT_STARTED}
        onClick={() => startAll()}
      >
        Start All
      </Button>
    ) : (
      <ButtonGroup isAttached variant={'solid'} size={'lg'}>
        <Button
          isDisabled={multiStopwatch.state != MultiStopwatchState.RUNNING}
          colorScheme={'red'}
          onClick={() => stopAll()}
        >
          Stop All
        </Button>
        <Button colorScheme={'yellow'} onClick={() => resetAll()}>
          Reset All
        </Button>
      </ButtonGroup>
    )}
  </Center>
);
