import { Center, Td, Text, VStack } from '@chakra-ui/react';
import React from 'react';
import { formatMs } from 'utils';

interface LapDataProps {
  laps: number[];
  startTime: number;
  index: number;
}

export default ({ laps, startTime, index }: LapDataProps) => (
  <Td m={0} p={0} pl={1} pr={1} border={'1px'} borderColor={'white'}>
    <Center>
      <VStack spacing={0}>
        <Text fontSize={'md'} as={'b'}>
          {formatMs(laps[index] - startTime)}
        </Text>
        <Text fontSize={'xs'}>{formatMs(laps[index] - (index ? laps[index - 1] : startTime))}</Text>
      </VStack>
    </Center>
  </Td>
);
